@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Verdana", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

/*adaptive for mobile and other width*/
@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
@custom-media --x2-resolution (resolution > 1.5dppx);
:root {
  --color__green: #68b738;
  --color__deep-green: #5eaa2f;
  --color__white: #ffffff;
  --color__black: #000000;
  --color__light-black: #111111;
  --color__grey: #f2f2f2;
  --color__deep-grey: #eaeaea;
  --color__dark-grey: #444444;
  --color__light-grey: #cdcdcd;
  --color__form-input-error: #ff8282;
  --color__outline: #e98411;
  --light-text: #222222;
}

.page :where(img) {
  max-width: 100%;
  object-fit: contain;
}

.page__body {
  position: relative;
  display: flex;
  min-width: 320px;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--font_mobi-size);
}
@media (--not-narrow-vp) {
  .page__body {
    font-size: var(--font_default-size);
  }
}

@media (--narrow-vp) {
  .page__header {
    padding-top: 10px;
  }
}
@media (--middle-vp) {
  .page__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 24px;
  }
}
@media (--wide-vp) {
  .page__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 53px;
  }
}

.page__main {
  flex: 1;
}

.page__centerer {
  margin: 0 20px;
}
@media (--middle-vp) {
  .page__centerer {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
  display: flex;
  padding-bottom: 13px;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color__white);
}
@media (--narrow-vp) {
  .header--noJS {
    display: grid;
  }
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: 0.3s;
}
.logo:focus-visible {
  outline: 2px solid var(--color__outline);
  outline-offset: 5px;
  border-radius: 20px;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo__icon {
  object-fit: cover;
}
@media (--narrow-vp) {
  .logo__icon {
    width: 33px;
    height: 37px;
  }
}
@media (--middle-vp) {
  .logo__icon {
    width: 60px;
    height: 52px;
  }
}
@media (--wide-vp) {
  .logo__icon {
    width: 70px;
    height: 60px;
  }
}

.logo__text {
  width: 105px;
  height: 19px;
  padding-left: 55px;
  transform: translateY(1px);
}
@media (--middle-vp) {
  .logo__text {
    padding-left: 10px;
    --fill: var(--color__dark-grey);
  }
}
@media (--wide-vp) {
  .logo__text {
    height: 22px;
    width: 132px;
    padding-left: 5px;
    --fill: var(--color__dark-grey);
  }
}

.menu-toggle {
  position: relative;
  top: 1px;
  display: block;
  width: 24px;
  height: 18px;
  background: transparent;
  border-top: 1.5px solid var(--color__black);
  border-bottom: 1.5px solid var(--color__black);
  border-left: none;
  border-right: none;
  font-size: 0;
  cursor: pointer;
  outline: none;
  transition: 0.2s;
  transition-timing-function: ease-in-out;
}
.menu-toggle:focus-visible {
  outline: 2px solid var(--color__outline);
  outline-offset: 5px;
}
@media (--not-narrow-vp) {
  .menu-toggle {
    display: none;
  }
}
@media (--narrow-vp) {
  .menu-toggle--noJS {
    display: none;
  }
}
.menu-toggle::before, .menu-toggle::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: var(--color__black);
  transform: translate(-50%, -50%);
  transition: 0.2s;
  transition-timing-function: ease-in-out;
}
.menu-toggle--active {
  border-color: transparent;
}
.menu-toggle--active::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.menu-toggle--active::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@keyframes showing {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes hiding {
  from {
    opacity: 1;
    visibility: visible;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@media (--narrow-vp) {
  .header-nav {
    position: absolute;
    top: 56px;
    width: 100vw;
    opacity: 0;
    z-index: 3;
    text-align: center;
    transform: translateX(-20px);
  }
  .header-nav--open {
    display: flex;
    flex-direction: column;
    animation-name: showing;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    animation-timing-function: ease;
  }
  .header-nav--closed {
    animation-name: hiding;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    animation-timing-function: ease;
  }
  .header-nav--noJS {
    position: static;
    display: flex;
    flex-direction: column;
    opacity: 1;
  }
}
@media (--not-narrow-vp) {
  .header-nav {
    width: 57%;
  }
}
@media (--wide-vp) {
  .header-nav {
    width: 32%;
    z-index: 2;
  }
}
@media (--wide-vp) {
  .header-nav--color_change {
    --changed-color: var(--color__white);
  }
}

.header-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--narrow-vp) {
  .header-nav__list {
    display: grid;
    background-color: var(--color__white);
    width: 100%;
    align-items: center;
  }
}
@media (--not-narrow-vp) {
  .header-nav__list {
    position: static;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: flex-end;
    column-gap: 9%;
    row-gap: 15px;
    transform: translateY(-5px);
    background-color: transparent;
    opacity: 1;
  }
}

@media (--narrow-vp) {
  .header-nav__item {
    position: relative;
    width: 100vw;
    display: flex;
    justify-content: center;
  }
  .header-nav__item::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eaeaea;
    content: "";
  }
  .header-nav__item::after {
    position: absolute;
    top: 100%;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eaeaea;
    content: "";
  }
}
@media (--not-narrow-vp) {
  .header-nav__item {
    justify-self: end;
  }
}

.header-nav__link {
  position: relative;
  display: block;
  width: max-content;
  color: var(--changed-color, --color__black);
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1.125rem;
  line-height: 1.3333333333;
  transition: 0.3s;
}
.header-nav__link:focus-visible {
  outline: 2px solid var(--color__outline);
  outline-offset: 1px;
  border-radius: 5px;
}
@media (--narrow-vp) {
  .header-nav__link {
    box-sizing: border-box;
    padding: 22px 24px;
    font-weight: 500;
  }
}
@media (--wide-vp) {
  .header-nav__link {
    font-size: 1.25rem;
    line-height: 1.482;
  }
}
@media (--not-narrow-vp) {
  .header-nav__link {
    max-width: 171px;
    text-align: center;
  }
  .header-nav__link--current::before {
    position: absolute;
    top: 135%;
    right: 0;
    z-index: 3;
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--color__green);
  }
}
@media (--not-narrow-vp) and (--wide-vp) {
  .header-nav__link--current::before {
    top: 110%;
  }
}
@media (--wide-vp) {
  .header-nav__link--changed_underline::before {
    background-color: var(--changed-color);
  }
}
.header-nav__link:hover {
  opacity: 0.6;
}
.header-nav__link:active {
  opacity: 0.3;
}

